.header_outer_container {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: rgb(20, 20, 20); */
  background-color: transparent;
  height: 10vh;
  color: white;
  transition: all 0.5s;
  transition-timing-function: ease-in;
  z-index: 1;
  background-color: black;
}
.nav__black {
  background-color: rgb(20, 20, 20);
}
.header_container {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header_left ul,
.header_right ul {
  width: 140%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  cursor: pointer;
}
.header_left ul li,
.header_right ul li {
  list-style: none;
}
.header_left ul img {
  width: 100px;
}

.header_right ul {
  gap: 20px;
}
